<template>
  <div>
    <el-card style="margin-bottom:10px">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>SIM卡管理</el-breadcrumb-item>
        <el-breadcrumb-item><a href="javascript:;">SIM卡列表</a></el-breadcrumb-item>
     </el-breadcrumb>
    </el-card>
    <el-card>
      <el-form :inline="true" :model="formInline" ref="formInline" :rules="rules">
        <el-form-item prop="prame1">
          <el-input v-model="formInline.param1" autofocus placeholder="请输入iccid、MSISDN">
            <el-button ref="search"  @click="onSubmit('formInline')" slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary">高级搜索</el-button>
          <el-button type="primary">号卡分配</el-button>
        </el-form-item>
        <el-form-item style="float:right">
          <el-button-group>
            <el-button  icon="el-icon-download"></el-button>
            <el-button @click="refreshTable()" icon="el-icon-refresh"></el-button>
            <el-button  icon="el-icon-upload2"></el-button>
          </el-button-group>
        </el-form-item>
      </el-form>
      <el-table
        :data="tableData.slice((currentPage-1)*pageSize,currentPage*pageSize)"
        style="width: 100%" v-loading="loading">
        <el-table-column label="操作" fixed="left" min-width="210">
          <template slot-scope="scope">
            <el-button type="primary" size="mini"  @click="handleEdit(scope.$index, scope.row)">编辑属性</el-button>
            <el-button type="primary" size="mini">变更套餐</el-button>
          </template>
        </el-table-column>
         <el-table-column label="ICCID" min-width="180">
          <template slot-scope="scope">
            <router-link to="/">{{ scope.row.iccid }}</router-link>
          </template>
        </el-table-column>
        <el-table-column label="MSISDN" min-width="180">
          <template slot-scope="scope">
            <router-link to="/">{{ scope.row.iccid }}</router-link>
          </template>
        </el-table-column>
        <el-table-column label="IMSI" min-width="180">
          <template slot-scope="scope">
            <router-link to="/">{{ scope.row.iccid }}</router-link>
          </template>
        </el-table-column>
        <el-table-column label="日期" min-width="180">
          <template slot-scope="scope">
            <i class="el-icon-time"></i>
            <span style="margin-left: 10px">{{ scope.row.date }}</span>
          </template>
        </el-table-column>
        <el-table-column label="姓名" width="180">
          <template slot-scope="scope">
            <el-popover trigger="hover" placement="top">
              <p>姓名: {{ scope.row.name }}</p>
              <p>住址: {{ scope.row.address }}</p>
              <div slot="reference" class="name-wrapper">
                <el-tag size="medium">{{ scope.row.name }}</el-tag>
              </div>
            </el-popover>
          </template>
        </el-table-column>
        <el-table-column label="操作" min-width="75" fixed="right">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)">删除
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        background
        :current-page="currentPage"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="pageTotal">
      </el-pagination>
      <el-dialog title="用户信息" :visible.sync="dialogFormVisible">
        <el-form :model="form">
          <el-form-item label="ICCID" label-width="60px">
            <el-input disabled v-model="form.iccid" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item  label="MSISDN" label-width="60px">
            <el-input disabled v-model="form.iccid" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="名字" label-width="60px">
            <el-input v-model="form.name" auto-complete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="modifyUser()">确 定</el-button>
        </div>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import {requestUserQuery} from '@/api/user'
export default {
  name: 'FuncTable',
  data() {
     let validatePrame1 = (rule, value, callback) => {
        if (value) {
          callback(new Error('请输入搜索条件'))
        } else {
          callback()
        }
      }
    return {
      formInline: {
        prame1: ''
      },
      rules: {
        prame1: [
          {validator: validatePrame1, trigger: 'blur'}
        ]
      },
      loading: false,
      tableData: [],
      currentPage: 1,
      pageSize: 5,
      pageTotal: 0,
      dialogFormVisible: false,
      form: {
        name: '',
        date: '',
        address: '',
        iccid: '',
        index: 0
      }
    }
  },
  created() {
    this.getData()
  },
  methods: {
    onSubmit (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.loading = true
          this.getData()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    refreshTable() {
      this.getData('refresh')
    },
    getData(methods) {
      this.loading =true
      requestUserQuery(this.formInline).then(res => {
            this.pageTotal = res.data.length
            this.tableData = res.data
            setTimeout(() => {
              this.loading = false
              if(!methods) {
                this.$message({
                  message: '查询成功！',
                  type: 'success'
                })
              }
              if(methods === 'refresh') {
                this.$message({
                  message: '刷新成功！',
                  type: 'success'
                })
              }
              
            }, 500)
          })
    },
    handleEdit (index, row) {
      this.form.index = index + (this.currentPage - 1) * this.pageSize
      this.form.name = row.name
      this.form.date = row.date
      this.form.address = row.address
      this.form.iccid = row.iccid
      this.dialogFormVisible = true
    },
    handleDelete (index, row) {
      this.tableData.splice(index + (this.currentPage - 1) * this.pageSize, 1)
      this.pageTotal = this.tableData.length
      this.$message({
        message: '删除' + row.name + '成功！',
        type: 'success'
      })
    },
    handleSizeChange (size) {
      this.pageSize = size
    },
    handleCurrentChange (currentPage) {
      this.currentPage = currentPage
    },
    modifyUser () {
      this.tableData[this.form.index].name = this.form.name
      this.tableData[this.form.index].date = this.form.date
      this.tableData[this.form.index].address = this.form.address
      this.dialogFormVisible = false
      this.$message({
        message: '修改' + this.form.name + '成功！',
        type: 'success'
      })
    }
  }
}
</script>

<style scoped>
.el-table {
  font-size: 13px;
}

</style>



